body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background-color: #ffffff;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.video-container {
  position: relative;
  display: flex;
  width: 100vw;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 50px); /* Reduce height to account for the tab menu */
  overflow: hidden;
}

video, img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.tab-menu {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.tab {
  color: rgb(87, 55, 8);
  flex: 1;
  text-align: center;
  padding: 10px 0;
  cursor: pointer;
}

.tab.active {
  background-color: #ffc400;
}
